export function parseFEN(fen) {
  // FEN format: "piecePlacement activeColor castling enPassant halfmove fullmove"
  const [piecePlacement] = fen.split(" ");
  const rows = piecePlacement.split("/");

  return rows.map((row) => {
    const squares = [];
    for (const char of row) {
      if (isNaN(char)) {
        squares.push(char);
      } else {
        // If char is a digit, push that many empty squares ('')
        for (let i = 0; i < Number(char); i++) {
          squares.push("");
        }
      }
    }
    return squares;
  });
}

export function parseMove(item) {
  if (item.slice(-2, -1) == "=") return item.slice(-4, -2);
  if (item.slice(-3, -2) == "=") {
    return item.slice(-5, -3);
  }
  if (item.slice(-1) == "+" || item.slice(-1) == "#") return item.slice(-3, -1);
  return item.slice(-2);
}

export function parseCurrentPlayer(fen) {
  return fen.split(" ")[1];
}

