import React from "react";

export function MoveList() {
  const temp = [
    { one: "e4", two: "f8" },
    { one: "e4", two: "f8" },
    { one: "e4", two: "f8" },
    { one: "e4", two: "f8" },
    { one: "e4", two: "f8" },
    // { one: "e4", two: "f8" },
    // { one: "e4", two: "f8" },
    // { one: "e4", two: "f8" },
    // { one: "e4", two: "f8" },
    // { one: "e4", two: "f8" },
  ];
  return (
    <div>
      <div className="moveList-title">Move List</div>
      <div className="moveList">
        {temp.map((item, index) => {
          return ListItem(item, index);
        })}
      </div>
    </div>
  );
}

function ListItem(data, index) {
  return (
    <div>
      {index + 1} + {data.one} +{data.two}
    </div>
  );
}
